.card {
  border-radius: 0.5rem;
  overflow: hidden;
  box-shadow: 0 0.625rem 1.25rem var(--dk-gray);
}
.card-community {
  padding: 2.1875rem 1.5625rem;
  background-color: var(--white);
}
@media (min-width: 40em) {
  .card-community {
    padding: 2.8125rem 2.5rem;
  }
}
.card-subscription {
  padding: 1.75rem 1.5625rem;
  background-color: var(--cyan);
}
@media (min-width: 40em) {
  .card-subscription {
    padding: 2.8125rem 2.5rem;
  }
}
.card-us {
  padding: 1.75rem 1.5625rem;
  background-color: var(--lt-cyan);
}
@media (min-width: 40em) {
  .card-us {
    padding: 2.8125rem 2.5rem;
  }
}
.card__header {
  font-weight: 700;
}
.card__header--cyan {
  color: var(--cyan);
  font-size: 1.25rem;
  margin-bottom: 1.25rem;
}
@media (min-width: 40em) {
  .card__header--cyan {
    font-size: 1.4375rem;
  }
}
.card__header--white {
  color: var(--white);
  font-size: 1.1875rem;
  margin-bottom: 1.25rem;
}
.card__subheader {
  font-weight: 700;
}
.card__subheader--day {
  color: var(--br-yellow);
  margin-bottom: 0.625rem;
  line-height: 1.5;
}
@media (min-width: 40em) {
  .card__subheader--day {
    font-size: 1.125rem;
  }
}
.card__subheader--price {
  margin: 0;
  margin-bottom: 0.625rem;
  display: inline-flex;
  align-items: center;
  gap: 0.625rem;
  color: var(--white);
  font-size: 1.75rem;
}
.card__subheader--price span {
  opacity: 0.5;
  font-size: 1rem;
  font-weight: 400;
}
.card__content {
  margin: 0;
  font-size: 0.875rem;
}
.card__content--gray {
  color: var(--grayish-blue);
  line-height: 1.8;
}
@media (min-width: 40em) {
  .card__content--gray {
    font-size: 1rem;
  }
}
.card__content--white {
  font-weight: 400;
  margin-bottom: 2.125rem;
}
.card__signup {
  display: block;
  border-radius: 0.3125rem;
  box-shadow: 0 0.3125rem 0.625rem var(--dk-cyan);
  text-align: center;
  text-decoration: none;
  color: var(--white);
  background-color: var(--br-yellow);
  font-weight: 700;
  padding: 0.9375rem;
  transition: opacity 300ms ease-in-out;
}
.card__signup:hover {
  opacity: 0.8;
}
.card__link {
  padding: 0;
  list-style-type: none;
  margin: 0;
}
.card__link li {
  font-size: 0.875rem;
  line-height: 1.5;
}
.card__link li a {
  font-weight: 400;
  color: var(--lt-gray);
  transition: color 300ms ease-in-out;
}
.card__link li a:hover {
  color: var(--br-yellow);
}